import React, {Component} from 'react';
import Mining from './Mining';
import Ban from './Ban.jpg';
import MiningPng from './Mining.png';
import './MiningContainer.css';

var xhr;
var renderData = [];

class MiningContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      ip_address: '...',
      region: '...',
      city: '...',
      location: '...',
      mining: "",
      start: 1,
      pusk: 0
    }

    this.processRequest = this.processRequest.bind(this);
    this.startMining = this.startMining.bind(this);
    this.continueMining = this.continueMining.bind(this);
  }

  componentDidMount() {
    xhr = new XMLHttpRequest();
    xhr.open("GET", "https://ipinfo.io/json?token=2277ce4bac0347", true);
    xhr.send();

    xhr.addEventListener("readystatechange", this.processRequest, false);
  }

  processRequest() {
    if (xhr.readyState === 4 && xhr.status === 200) {
      var response = JSON.parse(xhr.responseText);

      console.log(response);

      this.setState({
        ip_address: response.ip,
        region: response.region,
        city: response.city,
        location: response.loc
      });
    }
  }

  startMining(e) { 
    this.setState({
      start: 0,
      pusk: 1
    });

    setInterval(this.continueMining, 1000);
  }

  continueMining(e) {
    var numbers = Math.round(Math.random() * 1000);
    var numbersRe = Math.round(numbers / 1.75);
    var numbersNe = Math.round(numbers * 1.75);

    this.setState({
      mining: numbers + ' ' + numbersRe + ' ' + numbersNe
    });

    renderData.push(<Mining key={renderData.length} mining={this.state.mining} />);
  }
  
  render() {
    var startStyle = {
      opacity: this.state.start
    }

    var renderboxStyle = {
      opacity: this.state.pusk
    }

    return(
      <div className="mining">
        <div className="start" style={startStyle}>
          <h1>Срочно забань Рубена!</h1>
          <img src={Ban} className="ban" onClick={this.startMining} alt="Клик чтобы забанить!" />
        </div>
        <div className="renderBox" style={renderboxStyle}>
          <img src={MiningPng} alt="Начинаем майнить" />
          <p>IP-Адрес: {this.state.ip_address}</p>
          <p>Локация: {this.state.region} - {this.state.city} [{this.state.location}]</p>
          <div>
            {renderData}
          </div>
        </div>
      </div>
    );
  }
};

export default MiningContainer;